.photos-videos .photos-videos__photos {
  display: flex;
  justify-content: center;
}
.photos-videos__header {
  width: 280px;
  margin: 0 auto;
  padding-bottom: 45px;
  text-align: center;
}
.photos-videos__header .photos-videos__title {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 35px;
  font-size: 18px;
}
.photos-videos__header .photos-videos__text {
  width: 280px;
  margin: 0 auto;
  font-size: 14px;
}
@media (min-width: 768px) {
  .photos-videos__header {
    width: 458px;
    margin: 0 auto;
    padding-bottom: 63px;
    text-align: center;
  }
  .photos-videos__header .photos-videos__title {
    margin: 0 auto;
    margin-bottom: 35px;
    font-size: 30px;
  }
  .photos-videos__header .photos-videos__text {
    width: auto;
    margin: 0 auto;
  }
}
@media (min-width: 1280px) {
  .photos-videos__header {
    padding-bottom: 65px;
  }
}
